






































import {
	mapState
} from 'vuex'

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	},
	data() {
		return {
			lists: [{
				id: 'view',
				open: false,
				name: '蓝牙',
				url: 'get-bluetooth-tool'
			}, {
				name: '其他',
				open: false,
				url: 'get-bluetooth-tool'
			}]
		}
	},
	methods: {
		trigerCollapse(e) {
			for (var i = 0, len = this.lists.length; i < len; ++i) {
				if (e === i) {
					this.lists[i].pages ? this.lists[i].open = !this.lists[e].open : this.goDetailPage(this.lists[i]
						.url);
				} else {
					this.lists[i].pages ? this.lists[i].open = false : "";
				}
			}
		},
		goDetailPage(e) {
			let path = e.url ? e.url : e;
			let url = ~path.indexOf('platform') ? path : '/pages/API/' + path + '/' + path;
			uni.navigateTo({
				url: url
			})
		}
	}
}



